import React from 'react';

export function TutorialPopup({ onAccept, onSkip }) {
  return (
    <div className="fixed top-0 left-0 w-full h-full flex items-center justify-center backdrop-blur-md z-20">
      <div className="bg-white p-4 rounded-lg shadow-lg max-w-3xl text-center text-lg">
        <h2 className="text-xl font-bold mb-2">Tutorial</h2>
        <p className="text-gray-700">
          Welcome to your first InterEd lesson!<br/>
          Before you get started, do you want to go through a tutorial of how it works?
        </p>
        <div className="mt-4 w-full flex justify-center">
            <button className='bg-blue-400 hover:bg-blue-500 w-auto duration-200 text-white px-6 py-2 rounded-3xl mr-[10%]'
            onClick={onAccept}>Accept</button>
            <button className='bg-gray-400 hover:bg-gray-500 w-auto duration-200 text-white px-6 py-2 rounded-3xl'
            onClick={onSkip}>Skip</button>
        </div>
      </div>
    </div>
  );
}
