import React from 'react';

export const OS2Context = () => {

        function animation1() {
            window.parent.terminal.openFile('/home/example2/pipe1.c');
        }

        function animation2() {
                window.parent.terminal.sendInput("cd /home/example2\n");
                window.parent.terminal.sendInput("gcc -o pipe1 pipe1.c \n");
        }

        function animation3() {
                window.parent.terminal.sendInput("./pipe1 \n");
        }

        function animation4() {
                window.parent.terminal.openFile('/home/example2/pipe2.c');
        }

        function animation5() {
                window.parent.terminal.sendInput("cd /home/example2\n");
                window.parent.terminal.sendInput("gcc -o pipe2 pipe2.c \n");
        }

        function animation6() {
                window.parent.terminal.sendInput("./pipe2 \n");
        }

        function animation7() {
                window.parent.terminal.openFile('/home/example2/shm_create.c');
        }

        function animation8() {
                window.parent.terminal.sendInput("cd /home/example2\n");
                window.parent.terminal.sendInput("gcc -o shmcreate shm_create.c\n");
        }

        function animation9() {
                window.parent.terminal.sendInput("./shmcreate \n");
        }

        function animation10() {
                window.parent.terminal.openFile('/home/example2/shm_attach.c');
        }

        function animation11() {
                window.parent.terminal.sendInput("cd /home/example2\n");
                window.parent.terminal.sendInput("gcc -o shmattach shm_attach.c\n");
        }

        function animation12() {
                window.parent.terminal.sendInput("./shmattach \n");
        }

        function animation13() {
                window.parent.terminal.openFile('/home/example2/shm_delete.c');
        }

        function animation14() {
                window.parent.terminal.sendInput("cd /home/example2\n");
                window.parent.terminal.sendInput("gcc -o shmdelete shm_delete.c\n");
        }

        function animation15() {
                window.parent.terminal.sendInput("./shmdelete \n");
        }

        function animation16() {
                window.parent.terminal.openFile('/home/example2/sigchld.c');
        }

        function animation17() {
                window.parent.terminal.sendInput("cd /home/example2\n");
                window.parent.terminal.sendInput("gcc -o signal sigchld.c\n");
        }

        function animation18() {
                window.parent.terminal.sendInput("./signal \n");
        }



  return (
        <div className="w-full h-full text-lg">
                <div className='mb-2'>
                        <h1 className='text-2xl font-bold border-b-4 border-purple-400 tracking-wider inline'>2. IPC</h1>
                </div>
                <div className='flex flex-col'>
                        <h2 className='text-xl font-bold'>
                                1.1. Pipe
                        </h2>
                        <div className='mt-1 ml-2'>
                                A pipe can be created via the <code>pipe()</code> system call.
                                <div className="lesson_high"><code>pipe()</code></div>
                                <button className="lesson_button" onClick={animation1}><span>Go to the example</span></button>
                                <button className="lesson_button" onClick={animation2}><span>Compile the code</span></button>
                                <button className="lesson_button" onClick={animation3}><span>Run the code</span></button>
                        </div>
                </div>

                <hr className='w-full border my-4 border-gray-300'/>

                <div className='flex flex-col'>
                        <h2 className='text-xl font-bold'>
                                1.2. Filters
                        </h2>
                        <div className='mt-1 ml-2'>
                                Another example: Filters in shell command-line
                                <button className="lesson_button" onClick={animation4}><span>Go to the example</span></button>
                                <button className="lesson_button" onClick={animation5}><span>Compile the code</span></button>
                                <button className="lesson_button" onClick={animation6}><span>Run the code</span></button>
                        </div>
                </div>

                <hr className='w-full border my-4 border-gray-300'/>

                <div className='flex flex-col'>
                        <h2 className='text-xl font-bold'>
                                1.3. Shared Memory -- Creation
                        </h2>
                        <div className='mt-1 ml-2'>
                                Creation of shared memory
                                <button className="lesson_button" onClick={animation7}><span>Go to the example</span></button>
                                <button className="lesson_button" onClick={animation8}><span>Compile the code</span></button>
                                <button className="lesson_button" onClick={animation9}><span>Run the code</span></button>
                        </div>
                </div>

                <hr className='w-full border my-4 border-gray-300'/>
                <div className='flex flex-col'>
                        <h2 className='text-xl font-bold'>
                                1.4. Shared Memory -- Attach
                        </h2>
                        <div className='mt-1 ml-2'>
                                Attach shared memory to a process
                                <button className="lesson_button" onClick={animation10}><span>Go to the example</span></button>
                                <button className="lesson_button" onClick={animation11}><span>Compile the code</span></button>
                                <button className="lesson_button" onClick={animation12}><span>Run the code</span></button>
                        </div>
                </div>


                <hr className='w-full border my-4 border-gray-300'/>
                <div className='flex flex-col'>
                        <h2 className='text-xl font-bold'>
                        1.5. Shared Memory -- Delete
                        </h2>
                        <div className='mt-1 ml-2'>
                                Delete shared memory
                                <button className="lesson_button" onClick={animation13}><span>Go to the example</span></button>
                                <button className="lesson_button" onClick={animation14}><span>Compile the code</span></button>
                                <button className="lesson_button" onClick={animation15}><span>Run the code</span></button>
                        </div>
                </div>


                <hr className='w-full border my-4 border-gray-300'/>

                <div className='flex flex-col'>
                        <h2 className='text-xl font-bold'>
                                1.6. Signal
                        </h2>
                        <div className='mt-1 ml-2'>
                                Signal to the parent process when its child process terminates.
                                <button className="lesson_button" onClick={animation16}><span>Go to the example</span></button>
                                <button className="lesson_button" onClick={animation17}><span>Compile the code</span></button>
                                <button className="lesson_button" onClick={animation18}><span>Run the code</span></button>
                        </div>
                </div>
        </div>
  );
};
