import React from 'react';
import { useNavigate } from 'react-router-dom';

export const Tutorial = () => {

    const navigate = useNavigate();

    function goBack() {
        navigate(-1);
    }

  return (
    <div className="w-full h-full text-lg">
        <div className='mb-2'>
                <h1 className='text-2xl font-bold border-b-4 border-purple-400 tracking-wider inline'>1. InterEd Tutorial</h1>
        </div>
        <div className='flex flex-col'>
                <h2 className='text-xl font-bold'>
                        1.1. Basics
                </h2>
                <div className='mt-1 ml-2'>
                During your learning experience with InterEd, you will engage with virtual computer systems running the Linux operating system. These systems are equipped with specific dependencies tailored to each lesson's requirements.
<br/><br/>
Your primary means of interaction with these virtual machines will be through the Terminal, where you can execute commands, and occasionally using a file editor to manipulate files. It's crucial to keep in mind that these virtual machines have a temporary lifespan; they will automatically terminate once your lesson concludes. Consequently, any changes or configurations made within the environment will not persist, as the system resets after your session ends.
<br/><br/>
In addition to the Terminal and file editor, you will also have access to a lesson context window that provides guidance and assistance throughout your learning journey. This comprehensive environment is designed to facilitate your learning process effectively.
                </div>
        </div>

        <hr className='w-full border my-4 border-gray-300'/>
        <div className='flex flex-col'>
            <h2 className='text-xl font-bold'>
                1.2. The Practice Machine
            </h2>
            <div className='mt-1 ml-2'>
			    The interface of the practice machine has three "windows":
                <ul className='list-disc ml-6 block'>
				  <li> <b>Explorer</b> (left): It lists all files/folders, which you can add/delete/open. </li>
				  <li> <b>Editor</b> (right-top): When you open a file, the content of the file will be displayed in Editor, where you can view/write. </li>
				  <li> <b>Terminal</b> (right-bottom): It provides a command-line interface, where you can write down <code>shell</code> commands.</li>
                </ul>
            </div>
        </div>
        <hr className='w-full border my-4 border-gray-300'/>
        <div className='w-full flex justify-center pb-2'>
            <button className="lesson_button bg-gray-400 hover:bg-gray-500" onClick={goBack}>Go Back</button>
        </div>
    </div>
    );      
}